}

#cover
{
    max-width: 80vh;
    padding: 30px;
    margin: 0 auto 0 auto;
    background-color: #ff7575;
    border-radius: 20px;
    box-shadow: 0 10px 40px #ff7c7c, 0 0 0 20px #ffffffeb;
    transform: scale(0.6);
}

.search_input_form
{
    height: 96px;
}

.search_input[type="text"]
{
    width: 100%;
    height: 96px;
    font-size: 60px;
    line-height: 1;
}

.search_input[type="text"]::-webkit-input-placeholder
{
    color: #e16868;
}

.search_input[type="text"]:-ms-input-placeholder
{
    color: #e16868;
}

.search_input[type="text"]::-ms-input-placeholder
{
    color: #e16868;
}

.search_input[type="text"]::placeholder
{
    color: #e16868;
}

#s-cover
{
    width: 1px;
    padding-left: 35px;
}

.search_input_button
{
    position: relative;
    display: block;
    width: 84px;
    height: 96px;
    cursor: pointer;
}

.search_input_button:focus{
    outline: none;
}

#s-circle
{
    position: relative;
    top: -8px;
    left: 8px;
    width: 63px;
    height: 63px;
    margin-top: 0;
    border-width: 15px;
    border: 15px solid #fff;
    background-color: transparent;
    border-radius: 50%;
    transition: 0.5s ease all;
}

.search_input_button span
{
    position: absolute;
    top: 68px;
    left: 43px;
    display: block;
    width: 45px;
    height: 15px;
    background-color: transparent;
    border-radius: 10px;
    transform: rotateZ(52deg);
    transition: 0.5s ease all;
}

.search_input_button span:before, .search_input_button span:after
{
    content: '';
    position: absolute;
    bottom: 0;
    right: 0;
    width: 45px;
    height: 15px;
    background-color: #fff;
    border-radius: 10px;
    transform: rotateZ(0);
    transition: 0.5s ease all;
}

#s-cover:hover #s-circle
{
    top: -1px;
    width: 67px;
    height: 15px;
    border-width: 0;
    background-color: #fff;
    border-radius: 20px;
}

#s-cover:hover span
{
    top: 50%;
    left: 56px;
    width: 25px;
    margin-top: -9px;
    transform: rotateZ(0);
}

#s-cover:hover .search_input_button span:before
{
    bottom: 9px;
    transform: rotateZ(52deg);
}

#s-cover:hover .search_input_button span:after
{
    bottom: -11px;
    transform: rotateZ(-52deg);
}
#s-cover:hover .search_input_button span:before, #s-cover:hover .search_input_button span:after
{
    right: -6px;
    width: 40px;
    background-color: #fff;
}


