.loading:after {
    display: inline-block;
    -webkit-animation: dotty steps(1,end) 1s infinite;
            animation: dotty steps(1,end) 1s infinite;
    content: '';
  }

  @-webkit-keyframes dotty {
      0%   { content: ''; }
      25%  { content: '.'; }
      50%  { content: '..'; }
      75%  { content: '...'; }
      100% { content: ''; }
  }

  @keyframes dotty {
      0%   { content: ''; }
      25%  { content: '.'; }
      50%  { content: '..'; }
      75%  { content: '...'; }
      100% { content: ''; }
  }

