.arrow {
    width: 100px;
    height: auto;
    margin: 0 2rem;
    cursor: pointer;
    overflow: visible;
  }

  polygon, path {
    transition: all 0.5s cubic-bezier(.2,1,.3,1);
  }

  .disabled {
    fill: #d8cdcc;
  }



  .arrow:hover:not(.disabled) polygon{
    transition: all 1s cubic-bezier(.2,1,.3,1);
    fill: #FF4136;
  }

  .arrow:hover:not(.disabled) path{
    transition: all 1s cubic-bezier(.2,1,.3,1);
    fill: #FF4136;
    }

    .arrow:hover:not(.disabled) .arrow {
    -webkit-animation: arrow-anim 2.5s cubic-bezier(.2,1,.3,1) infinite;
            animation: arrow-anim 2.5s cubic-bezier(.2,1,.3,1) infinite;
  }

  .arrow:hover:not(.disabled) .arrow-fixed {
    -webkit-animation: arrow-fixed-anim 2.5s cubic-bezier(.2,1,.3,1) infinite;
            animation: arrow-fixed-anim 2.5s cubic-bezier(.2,1,.3,1) infinite;
  }

@-webkit-keyframes arrow-anim {
	0% {
		opacity: 1;
		transform: translateX(0);
	}
	5% {
		transform: translateX(-0.1rem);
	}
	100% {
		transform: translateX(1rem);
		opacity: 0;
	}
}

@keyframes arrow-anim {
	0% {
		opacity: 1;
		transform: translateX(0);
	}
	5% {
		transform: translateX(-0.1rem);
	}
	100% {
		transform: translateX(1rem);
		opacity: 0;
	}
}

@-webkit-keyframes arrow-fixed-anim {
	5% {
		opacity: 0;
	}
	20% {
		opacity: 0.4;
	}
	100% {
		opacity: 1;
	}
}

@keyframes arrow-fixed-anim {
	5% {
		opacity: 0;
	}
	20% {
		opacity: 0.4;
	}
	100% {
		opacity: 1;
	}
}


@media screen and (max-width: 600px) {
    .arrow{
        width: 60px;
    }
}
